.header {
  position: relative;
  width: 100%;
  display: flex;
  gap: 12px;
  justify-content: space-between;
  padding: 35px 0;
  margin-bottom: -100%;
  z-index: 2;

  @include vp-1023 {
    padding: 28px 0;
  }

  @include vp-767 {
    padding: 20px 0;
  }
}

.header__button {
  margin-top: 4px;
  padding: 5px 0;
}
